<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container @write="writePost = $event" :Image="Image" :Data="Data" :step="step"/>

  <button @click="more">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" multiple type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>

 <!-- <div v-if="step == 0">내용0</div>
 <div v-if="step == 1">내용1</div>
 <div v-if="step == 2">내용2</div>
 <button @click="step = 0">버튼0</button>
 <button @click="step = 1">버튼1</button>
 <button @click="step = 2">버튼2</button> -->


</template>

<script>
import Container from './components/Container.vue'
import postData from './components/postdata'
import axios from 'axios'
axios.post()

export default {
  name: 'App',
  data(){
    return{
      step : 0,
      Data : postData,
      morePlus : 0,
      Image : '',
      writePost : '',
      SelectFilter : '',
    }
  },
  mounted(){
    this.emitter.on('BoxClick',(a)=>{
      this.SelectFilter = a
    });
  },
  components: {
    Container,
  },
  methods: {
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.morePlus}.json`).then(result=>{
        console.log(result.data);
        this.Data.push(result.data);
        this.morePlus++;
      })
    },
    upload(e){
      let file  = e.target.files;
      console.log(file[0]);
      let url = URL.createObjectURL(file[0]);
      console.log(url);
      this.Image = url;
      this.step++;
    },
    publish(){
      var myPost = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.Image,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.writePost,
        filter: this.SelectFilter
    };
      this.Data.unshift(myPost);
      this.step = 0;
    }
  }
}
</script>

